
.address-edit-sec {
  width: 100%;
}
.loginTips .edit-list .edit-item {
  padding: 0.66667rem 0.8rem 0.66667rem 0;
  border-bottom: none;
}
.edit-list {
  padding-left: 0.66667rem;
  background-color: #fff;
}
.edit-list .edit-item {
    border-bottom: 1px solid #666666;
}
.edit-list .edit-item {
    display: flex;
    justify-content: space-between;
    padding: 0.8rem 0.8rem 0.4rem 0;
}
.edit-list .edit-item.no-bor {
      border: none;
}
.edit-list .edit-item .input {
      width: 100%;
}
.edit-list .edit-item .input.code-input {
        padding-bottom: 0.4rem;
        width: 65%;
        border-bottom: 1px solid #666666;
}
.edit-list .edit-item input::-webkit-input-placeholder {
      color: #BDBDBD;
}
.code {
  display: inline-block;
  border: 1px solid #666;
  color: #BDBDBD;
  background-color: #fff;
  width: 2.4rem;
  height: 1.01333rem;
}
.btn-save {
  margin: 1.33333rem auto;
  display: block;
  width: 87%;
  height: 1.33333rem;
  color: #fff;
  border: none;
  line-height: 1;
  font-size: 0.37333rem;
  background-color: #D8331D;
}
.address-popup {
  width: 100%;
}
.picker-items {
  width: 100%;
}
.flex-bar {
  padding: 0 0.4rem;
  display: flex;
  justify-content: space-around;
  height: 1.33333rem;
  line-height: 1.33333rem;
  border-bottom: 1px solid #eee;
}
.flex-title {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}
.edite {
  display: inline-block;
  width: 0.8rem;
  color: #666;
}



