
.right-nav-wap {
  padding: 1.33333rem 0.4rem 1.46667rem 0;
  display: inline-block;
  width: 7.44rem;
  height: 100%;
  background: #fff;
  top: 50%;
  right: 0;
  bottom: auto;
  left: auto;
  -webkit-transform: translate3d(0, -50%, 0);
  transform: translate3d(0, -50%, 0);
  opacity: .96;
}
.right-nav-content {
  padding-top: 1.6rem;
  width: 100%;
  height: 100%;
  background-color: #eeeeee;
}
.right-nav-content .nav-list {
    width: 100%;
    font-size: 0.37333rem;
}
.right-nav-content .nav-list li .nav-item {
      padding: 0.24rem 0 0.24rem 1.86667rem;
      font-weight: bold;
}
.right-nav-content .nav-list li .nav-item:active {
        background-color: #fff;
}
.right-nav-content .nav-list .sub-nav-list {
      margin-bottom: 0.6rem;
      font-size: 0.32rem;
}
.right-nav-content .nav-list .sub-nav-list li {
        padding: 0.24rem 0 0.24rem 2.18667rem;
}
.right-nav-content .nav-list .sub-nav-list li:active {
          background-color: #fff;
}



