
@charset "UTF-8";
* {
  box-sizing: border-box;
}
html, body, #app {
  width: 100%;
  min-height: 100%;
  height: 100%;
}
input::-webkit-input-placeholder {
  color: #dedede;
}
[v-cloak] {
  display: none;
}
input {
  border: none;
  color: #191919;
}
body {
  font-size: 0.32rem;
  color: #191919;
}
.vt-middle {
  vertical-align: middle;
}
a {
  color: #191919;
}
.pd30 {
  padding: 0 0.4rem;
}
.text-red {
  color: #D8331D;
}
.text-grey {
  color: #BDBDBD;
}
.text-light-grey {
  color: #666;
}
.grey-bg-sec {
  padding-bottom: 0.26667rem;
  background-color: #eee;
}
.default-bg {
  padding-bottom: 0.53333rem;
  background-color: #eee;
  width: 100%;
  min-height: 100%;
}
.default-panel {
  width: 100%;
  padding: 0 0 1.46667rem 0;
  font-size: 0.32rem;
}
.cell-header {
  margin-top: 0.8rem;
  display: flex;
  justify-content: space-between;
  font-size: 0.37333rem;
}
.cell-header .links {
    color: #BDBDBD;
    font-size: 0.32rem;
}
.cell-header .icon-arrow-right {
    margin-left: 0.26667rem;
}

/****文字list start*****/
.text-list {
  margin-top: 0.4rem;
  font-size: 0.16rem;
}
.text-list .mint-cell {
    min-height: 1.2rem;
}
.text-list .mint-cell:active {
      background: #ddd;
}
.text-list .mint-cell .mint-cell-wrapper {
      background-image: none;
      padding-left: 0.96rem;
      font-size: 0.32rem;
}
.text-list .mint-cell .mint-cell-wrapper:last-child {
        background-image: none;
}

/****文字list end*****/
/****商品列表 start*****/
.pro-list .cell-header {
  padding-bottom: 0.26667rem;
  border-bottom: 1.5px solid #CCCCCC;
}
.pro-group {
  padding: 0 0.33333rem;
  display: flex;
  flex-wrap: wrap;
  font-size: 0;
}
.pro-group.first {
    margin-top: 0.53333rem;
}
.pro-group .pro-item {
    margin-right: 0.53333rem;
    width: 4rem;
    font-size: 0;
}
.pro-group .pro-item:nth-of-type(2n) {
      margin-right: 0;
}
.pro-group .pro-item img {
      max-width: 100%;
      width: 100%;
}
.pro-group .pro-item .pro-details {
      padding: 0.53333rem 0 0.74667rem 0;
      font-size: 0.32rem;
      text-align: center;
}
.pro-group .pro-item .pro-details .title {
        display: block;
        width: 100%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
}
.pro-group .pro-item .pro-details .price {
        display: block;
        margin-top: 0.13333rem;
        color: #d8331d;
}
.operate-group .text-counter {
  margin: 0 0.53333rem;
}

/****商品列表 end*****/
/****购物车商品 横排样式 start*****/
.pro-table-sec {
  padding: 0 0.53333rem;
  font-size: 0.32rem;
  background-color: #fff;
}
.pro-table {
  padding: 0.53333rem 0;
  width: 100%;
  display: flex;
  border-bottom: 1px solid #eee;
}
.pro-table .pro-table-left {
    margin-right: 0.53333rem;
    width: 0.48rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}
.pro-table .pro-table-left .checkbox-group {
      margin-right: 0;
}
.pro-table .pro-table-right {
    flex: 1;
}
.pro-table-footer {
  padding: 0.4rem 0 0.4rem 0.34667rem;
  display: flex;
  justify-content: space-between;
  color: #BD8D46;
}
.pro-table-footer .birth-brand {
    width: 1.33333rem;
    font-size: 0.26667rem;
    color: #666;
}
.pro-table-footer .text-grey {
    flex: 1;
    text-align: right;
}
.pro-table-footer .icon-arrow-right {
    margin-left: 0.26667rem;
}
.media {
  display: flex;
}
.media .media-left {
    width: 2.13333rem;
    height: 2.13333rem;
}
.media .media-left img {
      width: 100%;
      height: 100%;
}
.media .media-content {
    flex: 1;
    padding: 0.13333rem 0 0 0.53333rem;
    position: relative;
    width: 1px;
}
.media .media-content p {
      line-height: 1;
}
.media .media-content .m-title {
      color: #000;
      position: relative;
      padding-right: 0.66667rem;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
}
.media .media-content .m-title .m-text {
        padding-right: 0.93333rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}
.media .media-content .m-title .m-small {
        color: #B2B2B2;
        font-size: 0.26667rem;
        position: absolute;
        top: 0;
        right: 0;
}
.media .media-content .m-subTitle {
      margin-top: 0.13333rem;
      padding: 0.06667rem 0.18667rem 0.06667rem 0;
      display: inline-block;
      line-height: 1;
      font-size: 0.21333rem;
}
.media .media-content .m-subTitle .label {
        padding: 0.06667rem;
        margin-right: 0.13333rem;
        background-color: #D4B675;
        color: #fff;
}
.media .media-content .m-operate {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      padding: 0 0.53333rem;
}
.media .media-content .m-operate .m-operate-cont {
        display: flex;
        justify-content: space-between;
}
.media .media-content .m-operate .m-operate-cont .text-red {
          line-height: 0.53333rem;
}

/****购物车商品 横排样式 end*****/
.icon-adress {
  display: inline-block;
  width: 0.26667rem;
  height: 0.45333rem;
  background: url("../images/icon-adress.png") no-repeat center center;
  background-size: 100%;
  vertical-align: middle;
}
.icon-arrow-right {
  display: inline-block;
  width: 0.21333rem;
  height: 0.29333rem;
  background: url("../images/arrow-right.png") no-repeat center center;
}
.icon-goback {
  display: inline-block;
  width: 0.26667rem;
  background: url("../images/arrow-goback.png") no-repeat center center;
  vertical-align: middle;
}
.icon-nav {
  display: inline-block;
  width: 0.46667rem;
  height: 0.37333rem;
  border-top: 2px solid #191919;
  border-bottom: 2px solid #191919;
  position: relative;
  z-index: 999999;
}
.icon-nav:before {
    content: '';
    width: 110%;
    height: 2px;
    background-color: #191919;
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -1px;
}
.icon-cart {
  padding-left: 0.10667rem;
  display: inline-block;
  width: 0.66667rem;
  height: 0.53333rem;
  background: url("../images/icon-cart.png") no-repeat center center;
  background-size: contain;
  vertical-align: middle;
}
.icon-reduce, .icon-plus {
  display: inline-block;
  width: 0.53333rem;
  height: 0.53333rem;
  border: 1px solid #666;
  position: relative;
  vertical-align: middle;
}
.icon-reduce:before, .icon-plus:before {
    content: '';
    width: 46%;
    height: 1px;
    position: absolute;
    top: 50%;
    left: 27%;
    background: #666;
}
.icon-plus:after {
  content: '';
  width: 1px;
  height: 46%;
  position: absolute;
  left: 50%;
  top: 27%;
  margin-left: -1px;
  background: #666;
}
.icon-closed {
  display: inline-block;
  width: 0.30667rem;
  height: 0.30667rem;
  position: relative;
  vertical-align: middle;
  background: url("../images/icon-closed.png") no-repeat center center;
  background-size: 100%;
}
.icon-arrow-down {
  display: inline-block;
  width: 0.29333rem;
  height: 0.29333rem;
  background: url("../images/arrow-right.png") no-repeat center center;
  transform: rotate(90deg);
}
.icon-arrow-up {
  transform: rotate(-90deg);
}
.icon-wx {
  margin: 0 0.46667rem 0 0.26667rem;
  display: inline-block;
  width: 0.57333rem;
  height: 0.53333rem;
  background: url("../images/icon-wx.png") no-repeat 0 0;
  background-size: 100%;
  vertical-align: middle;
}
.icon-zfb {
  margin: 0 0.46667rem 0 0.26667rem;
  display: inline-block;
  width: 0.58667rem;
  height: 0.58667rem;
  background: url("../images/icon-zfb.png") no-repeat 0 0;
  background-size: 100%;
  vertical-align: middle;
}
.icon-edite {
  margin: 0 0.46667rem 0 0.26667rem;
  display: inline-block;
  width: 0.45333rem;
  height: 0.46667rem;
  background: url("../images/icon-edite.png") no-repeat 0 0;
  background-size: 100%;
}
.checkbox-group {
  margin-right: 0.21333rem;
  display: inline-block;
  width: 0.56rem;
  height: 0.57333rem;
  position: relative;
}
.checkbox-group > .input-checkbox {
    -webkit-appearance: checkbox;
    width: 120%;
    height: 120%;
    vertical-align: middle;
    position: relative;
    z-index: 9999;
    opacity: 0;
}
.checkbox-group > .input-checkbox + .icon-checkbox {
      display: inline-block;
      width: 100%;
      height: 100%;
      background: url("../images/icon-checkbox.png") no-repeat 0 0;
      position: absolute;
      background-size: contain;
      top: 0;
      left: 0;
}
.checkbox-group > .input-checkbox:checked + .icon-checkbox {
      background: url("../images/icon-checkbox-checked.png") no-repeat 0 0;
      background-size: contain;
}
.radio-group {
  margin-right: 0.21333rem;
  display: inline-block;
  width: 0.21333rem;
  height: 0.21333rem;
  position: relative;
}
.radio-group .input-radio {
    -webkit-appearance: radio;
    width: 100%;
    height: 100%;
    vertical-align: middle;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
    opacity: 0;
}
.radio-group .input-radio + .icon-radio {
      display: inline-block;
      width: 100%;
      height: 100%;
      position: absolute;
      background-size: 100%;
      top: 0;
      left: 0;
      background: url("../images/icon-radio.png") no-repeat;
      background-size: contain;
}
.radio-group .input-radio:checked + .icon-radio {
      background: url("../images/icon-radio-checked.png") no-repeat;
      background-size: contain;
}
.pay-item {
  margin-left: 0.26667rem;
  display: inline-block;
  width: 2.13333rem;
  height: 0.8rem;
  background-color: #D8331D;
  color: #fff;
}
.default-btn {
  width: 2.13333rem;
  height: 0.8rem;
  border: 1px solid #ddd;
  background-color: #fff;
}

/*********mint-msgbox start**************/
.mint-msgbox {
  border-radius: 0;
  font-size: 0.32rem;
}
.mint-msgbox .mint-msgbox-content {
    padding: 0.66667rem 0.66667rem 0.66667rem 0.66667rem;
    border-bottom: none;
}
.mint-msgbox-btns {
  height: 1.01333rem;
}
.mint-msgbox-cancel {
  color: #fff;
  border-right: none;
  background-color: #CCCCCC;
}
.mint-msgbox-confirm {
  color: #fff;
  background-color: #D8331D;
}
.mint-msgbox-input input {
  padding: 0.26667rem;
  border-radius: 0;
}

/*********mint-msgbox  end**************/
/*********address  end**************/
.address-sec {
  padding: 0.53333rem 0.66667rem;
  display: flex;
  width: 100%;
  justify-content: space-between;
  font-size: 0.29333rem;
  background-color: #fff;
  align-items: center;
  border-bottom: 1px solid #ccc;
  color: #191919;
}
.address-sec .big-size {
    font-size: 0.37333rem;
    color: #191919;
}
.address-sec .big-size .last {
      margin-left: 0.13333rem;
}
.address-sec .address-content {
    line-height: 1.2em;
    max-width: 80%;
}
.address-sec .address-content p {
      margin-top: 0.13333rem;
}
.address-sec .address-content .title {
      font-size: 0.26667rem;
      color: #666;
}
.address-sec .address-content .phone {
      font-size: 0.29333rem;
}

/*********address  end**************/
.picker {
  width: 100%;
  background-color: #fff;
}
.picker-toolbar {
  width: 100%;
  line-height: 0.66667rem;
  display: flex;
  justify-content: space-between;
  text-align: center;
  color: #544014;
}
.picker-toolbar > span {
    display: inline-flex;
    flex: 1;
    justify-content: center;
    align-items: center;
}
.picker-items {
  background-color: #eee;
}
.picker-item {
  color: #BDBDBD;
  text-align: center;
  font-size: 0.32rem;
}
.picker-item.picker-selected {
  color: #544014;
  font-size: 0.4rem;
}



