
.pay-list {
  margin-top: 0.26667rem;
  padding-left: 0.66667rem;
  background-color: #fff;
}
.pay-list li {
    padding: 0.53333rem 0;
}
.pay-list li .pay-items {
      display: flex;
      align-items: center;
}
.pay-list li .pay-items .checkbox-group {
        width: 0.48rem;
        height: 0.50667rem;
}
.pay-list li:not(:last-of-type) {
      border-bottom: 1px solid #BDBDBD;
}
.btn-sure {
  margin: 1.33333rem auto;
  display: block;
  width: 8.74667rem;
  height: 1.33333rem;
  color: #fff;
  background-color: #D8331D;
  border: none;
  font-size: 0.37333rem;
}



