
input, button, select, textarea {
  outline: none;
  border: none;
}
textarea {
  resize: none;
}
#search {
  height: 1.33333rem;
  position: relative;
}
#search-img {
  display: inline-block;
  margin-left: 0.8rem;
  margin-top: 0.46667rem;
  height: auto;
  width: 0.4rem;
}
#search-text-view {
  display: inline-block;
  position: absolute;
  left: 1.65333rem;
  top: 0px;
  bottom: 0px;
}
#search-text {
  position: absolute;
  top: 0.42667rem;
  width: 6.66667rem;
}
#search-close {
  position: absolute;
  right: 0.98667rem;
  width: 0.26667rem;
  height: auto;
  top: 0.53333rem;
}
#header {
  /*height: pxToRem(386px);*/
}
#header-localPosition {
  height: 1.06667rem;
  /*background-color: black;*/
  border-bottom: solid 1px #d8d8d8;
  position: relative;
}
#header-localPosition-img {
  width: 0.26667rem;
  display: inline;
  position: absolute;
  left: 0.53333rem;
  top: 0.32rem;
}
.header-lp-city {
  display: inline;
  position: absolute;
  left: 1.06667rem;
  top: 0px;
  bottom: 0px;
}
.header-localPosition-lable {
  display: inline;
  color: #191919;
  line-height: 1.06667rem;
  vertical-align: middle;
  font-size: 0.32rem;
}
.header-localPosition-lable2 {
  color: #CCCCCC;
  font-size: 0.32rem;
  line-height: 1.06667rem;
}
#header-hotCity-center {
  margin-left: 0px;
  margin-right: 20px;
}
.header-hotCity-lable1 {
  /*position:absolute;*/
  left: 0.53333rem;
  margin-left: 0.53333rem;
  margin-top: 0.8rem;
  font-size: 0.32rem;
}
.header-hotCity-hotBtn {
  /*flex:1 20%;*/
  width: 2.4rem;
  text-align: center;
  margin-left: 0.53333rem;
  height: 0.8rem;
  margin-bottom: 0.26667rem;
  color: #191919;
  padding-top: 0.16rem;
  /*line-height: pxToRem(40px);*/
  border: 0.5px solid #666666;
  font-size: 0.32rem;
}
.header-hotCity-hotBtnView {
  display: flex;
  flex-flow: row wrap;
  margin-top: 0.26667rem;
  margin-bottom: 0.53333rem;
}
.cell {
  background-color: white;
  text-align: left;
  height: 0.82667rem;
  line-height: 0.82667rem;
  width: 100%;
  vertical-align: middle;
  font-size: 0.32rem;
  padding-left: 0.53333rem;
}
.cell-child {
  padding-left: 0.8rem;
}
.wrapper {
  height: 0.85333rem;
  padding-right: 20px;
  position: relative;
}
.sparator {
  /*width: 100%;*/
  height: 1px;
  position: absolute;
  bottom: 0px;
  left: 0.24rem;
  right: 0px;
  background-color: #d8d8d8;
}
.sparator-child {
  left: 0.74667rem;
}
.search-sparator {
  left: 0rem;
}
.mint-indexsection-index {
  height: 0.65333rem;
  background-color: #eeeeee;
  text-align: left;
  padding-left: 0.53333rem;
  padding-top: 0.13333rem;
  color: #bd8d46;
  font-size: 0.32rem;
}
#section-header {
  height: 0.33333rem;
  background-color: #eeeeee;
}
.mint-indexlist-nav {
  border-left: solid 0px #ddd;
}



