
.detailsHeader {
  padding: 0 0.4rem;
  display: flex;
  justify-content: space-between;
  height: 1.33333rem;
  line-height: 1.33333rem;
  background-color: #fff;
  opacity: .9;
}
.detailsHeader .vt-middle {
    display: inline-block;
    padding: 0 10%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellpsis;
    width: 1px;
}
.details-title {
  padding: 0 10%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.icon-cart-group {
  display: flex;
  align-items: center;
}
.icon-cart-group .icon-cart {
    text-align: center;
    line-height: 0.53333rem;
    color: #666;
    font-size: 0.26667rem;
}



