
.page-lazyload-image[lazy=loading] {
  width: 100% !important;
  height: 300px;
  background-color: rgba(0, 0, 0, 0.3);
}
.prolist-panel {
  background-color: #eee;
}
.bg-sec {
  background-color: #fff;
}
.page-infinite-listitem {
  height: 150px;
  line-height: 150px;
}
.pro-img-swiper {
  width: 100%;
  height: 10rem;
}
.pro-img-swiper .pro-slide img {
    max-width: 100%;
    min-height: 100%;
}
.pro-img-swiper .mint-swipe-indicator {
    background-color: white;
    opacity: 0.5;
    transform: scale(0.8);
}
.pro-img-swiper .mint-swipe-indicator.is-active {
      background-color: white;
      opacity: 1;
      transform: scale(1);
}
.pro-titles {
  padding: 0.8rem 0;
  text-align: center;
  border-bottom: 1px solid #ccc;
}
.pro-titles h1 {
    font-size: 0.37333rem;
    font-weight: normal;
    line-height: 1.2em;
}
.pro-titles .text-red, .pro-titles .text-grey {
    margin-top: 0.26667rem;
    line-height: 1;
}
.pro-titles .text-grey {
    font-size: 0.26667rem;
}
.pro-spec {
  padding: 0.8rem 0.13333rem 0 0.13333rem;
}
.pro-spec .spec-list {
    margin-top: 0.26667rem;
    font-size: 0;
}
.pro-spec .spec-list .item-spec {
      margin-bottom: 0.26667rem;
      display: inline-block;
      width: 1.86667rem;
      height: 0.8rem;
      line-height: 0.8rem;
      text-align: center;
      font-size: 0.34667rem;
      border: 1px solid #666;
}
.pro-spec .spec-list .item-spec:not(:last-of-type) {
        margin-right: 0.48rem;
}
.pro-spec .spec-list .item-spec.active {
        background-color: #D8331D;
        color: #fff;
        border-color: #D8331D;
}
.pro-spec .spec-details {
    margin-top: 0.53333rem;
    padding: 0.26667rem 0.21333rem 0.8rem 0.21333rem;
    color: #666666;
    border-top: 1px solid #EEEEEE;
}
.pro-spec .spec-details .spec-details-list {
      font-size: 0;
}
.pro-spec .spec-details .spec-details-list li {
        padding: 0.13333rem;
        display: inline-block;
        font-size: 0.26667rem;
        vertical-align: top;
        width: 50%;
}
.counter-operate {
  padding: 0.53333rem 0.13333rem;
  display: flex;
  justify-content: space-between;
  line-height: 0.53333rem;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
.counter-operate .text-counter {
    margin: 0 0.53333rem;
}
.pro-help {
  padding: 0.53333rem 0.13333rem;
  display: flex;
  justify-content: space-between;
}
.pro-help .item-help {
    border-bottom: 1px solid #191919;
}
.pro-details {
  margin-top: 0.8rem;
  padding-top: 1.06667rem;
  text-align: center;
}
.pro-details .title {
    font-size: 0.32rem;
    font-weight: normal;
}
.pro-details .pro-details-info {
    margin-top: 0.77333rem;
    padding-bottom: 1.06667rem;
}
.pro-details .pro-details-info img {
      width: 100%;
      max-width: 100%;
}
.pro-footer {
  position: fixed;
  display: flex;
  width: 100%;
  bottom: 0;
  left: 0;
  text-align: center;
}
.pro-footer .i-cart, .pro-footer .i-buynow {
    flex: 1;
    line-height: 1.46667rem;
    color: #fff;
    font-size: 0.42667rem;
}
.pro-footer .i-cart {
    background-color: #D4B675;
}
.pro-footer .i-buynow {
    background-color: #D8331D;
}
.promise-popup {
  width: 100%;
  text-align: center;
  background-color: #EEEEEE;
}
.promise-popup .promise-sec {
    padding: 0.53333rem 0;
}
.promise-popup .promise-sec .promise-line {
      padding: 0.21333rem 0;
      line-height: 1;
}
.promise-popup .promise-sec .i-b {
      font-weight: bold;
}
.promise-popup .title {
    padding: 0.21333rem 0;
    font-size: 0.37333rem;
}
.promise-popup .promise-btn {
    padding: 0.48rem 0;
    display: inline-block;
    width: 100%;
    background-color: #fff;
}



