
.paySuc-sec {
  text-align: center;
  color: #9B9B9B;
}
.paySuc-sec .tips {
    display: inline-block;
    margin-top: 0.53333rem;
    max-width: 6.02667rem;
}
.suc-img {
  margin-top: 0.8rem;
  display: inline-block;
  width: 4.26667rem;
  height: 4.26667rem;
  color: #fff;
  position: relative;
}
.suc-img .text {
    display: block;
    width: 100%;
    position: absolute;
    top: 0.66667rem;
    left: 0;
    font-size: 0.37333rem;
}
.suc-img img {
    width: 100%;
}
.suc-operate-group {
  margin-top: 1.33333rem;
  font-size: 0;
}
.suc-operate-group .op-items {
    display: inline-block;
    background-color: #fff;
    padding: 0.26667rem 0.66667rem;
    border: 1px solid #666;
    font-size: 0.32rem;
    line-height: 1;
    color: #191919;
}
.suc-operate-group .op-items:not(:last-of-type) {
      margin-right: 0.8rem;
}



