/*img[src=""]{*/
/*opacity: 0;*/
/*}*/
input[type="radio"] {
  margin: 3px 3px 0px 5px;
  display: none;
}
.userAccountSet-sparator {
  height: 1px;
  background-color: #d8d8d8;
  margin-left: 0.69333rem;
}
.defaultAccountSet-cell {
  font-size: 0.32rem;
  margin-left: 0.69333rem;
  margin-right: 0px;
  height: 1.8rem;
  display: flex;
  justify-content: space-between;
  /*line-height: pxToRem(135px);*/
}
.defaultAccountSet-cell1 {
  height: 2.93333rem;
  /*line-height: pxToRem(220px);*/
}
.cell-rightView {
  margin-right: 0.69333rem;
  text-align: right;
  line-height: 1.8rem;
}
.cell-rightView1 {
  line-height: 2.93333rem;
  position: relative;
}
.cell-leftTitle {
  color: #BDBDBD;
  display: inline-block;
  height: 1.8rem;
  line-height: 1.8rem;
}
.cell-leftTitle1 {
  height: 2.93333rem;
  line-height: 2.93333rem;
}
#defaultAccountSet-userIcon {
  height: 1.86667rem;
  width: 1.86667rem;
  margin-top: 0.53333rem;
  background-color: #EEEEEE;
}
#genderChoice1 {
  background-color: #4caf50;
  width: 50px;
  height: 50px;
}
#genderChoice2 {
  background-color: #4caf50;
  width: 50px;
  height: 50px;
}
#defaultAccountSet-cell-location {
  display: inline-block;
  width: 0.24rem;
  margin-left: 0.26667rem;
  /*padding-top: 40px;*/
  vertical-align: text-top;
}
input[type="file"] {
  color: transparent;
}
.upload-open-photo {
  opacity: 0;
  background-color: #26a2ff;
  position: absolute;
  left: 0px;
  width: 1.86667rem;
  bottom: 0px;
  top: 0px;
  /*z-index: 100;*/
}
.userAccountSet-textfield {
  text-align: right;
}
.sex-list {
  margin-top: 20px;
  margin-left: 0.8rem;
}
.sex-list li {
    line-height: 0.85333rem;
    list-style-type: none;
    float: left;
}
.sex-list li:last-of-type {
      padding-left: 0.4rem;
}
.v-modal {
  background-color: #191919;
  opacity: 0.3;
}
.mint-datetime-action {
  width: 100px;
  line-height: 1.33333rem;
  color: #191919;
  font-size: 0.32rem;
}
.picker-toolbar {
  height: 1.33333rem;
  align-items: center;
}
.picker-items {
  background-color: #eeeeee;
}
.picker-center-highlight:before, .picker-center-highlight:after {
  background-color: #BDBDBD;
}



