
.banner-group {
  /*width: pxToRem(690px);*/
  height: 9.86667rem;
  margin-left: 0.4rem;
  margin-right: 0.4rem;
}
.banner-group .banner-slide img {
    /*max-width: 100%;*/
    width: 100%;
    height: 100%;
}
.banner-group .mint-swipe-indicator {
    background-color: white;
    opacity: 0.5;
    transform: scale(0.75);
}
.banner-group .mint-swipe-indicator.is-active {
      background-color: white;
      opacity: 1;
      transform: scale(1);
}
.home-content-cellHeader {
  height: 0.98667rem;
  margin-top: 1.06667rem;
  margin-left: 0.4rem;
  margin-right: 0.4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.home-content-cellHeaderHr {
  margin-left: 0.4rem;
  margin-right: 0.4rem;
  margin-bottom: 0.53333rem;
}
.home-content-cellContent {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
}
.home-content-cell {
  width: 4rem;
  height: 5.54667rem;
  margin-right: 0.26667rem;
  margin-left: 0.26667rem;
  margin-bottom: 0.8rem;
}
.home-content-cellImg {
  width: 4rem;
  height: 4rem;
}
.home-content-cellNameText {
  margin-top: 0.53333rem;
  font-size: 0.32rem;
  /*height: pxToRem(26px);*/
  text-align: center;
  color: black;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: 4rem;
}
.home-content-cellPriceText {
  font-size: 0.32rem;
  margin-top: 0.05333rem;
  text-align: center;
  color: #d8331d;
}
.home-content-right {
  vertical-align: -4%;
}
.home-content-rightText {
  color: #BDBDBD;
  font-size: 0.32rem;
  margin-right: 0.13333rem;
}
.home-content-typeName {
  color: #191919;
  font-size: 0.32rem;
}



