
.firm-panel .address-sec {
  margin-top: -1px;
  padding: 0.8rem 0.66667rem;
  display: flex;
  justify-content: space-between;
  font-size: 0.29333rem;
  background-color: #fff;
  align-items: center;
  border-top: 1px solid #D4B675;
  border-bottom: none;
}
.firm-panel .address-sec .big-size {
    font-size: 0.37333rem;
}
.firm-panel .address-sec .big-size .last {
      margin-left: 0.13333rem;
}
.firm-panel .address-sec .address-content {
    line-height: 1.2em;
}
.firm-panel .address-sec .address-content p {
      margin-top: 0.13333rem;
}
.firm-panel .address-sec .address-content .title {
      font-size: 0.26667rem;
      color: #666;
}
.extra-sec {
  background: #fff;
}
.extra-sec .text-list .text {
    color: #191919;
}
.extra-sec .text-list .mint-cell {
    padding-left: 0.66667rem;
    min-height: 1.6rem;
}
.extra-sec .text-list .mint-cell:active {
      background: #fff;
}
.extra-sec .text-list .mint-cell:not(:last-of-type) .mint-cell-wrapper {
      border-bottom: 1px solid #bdbdbd;
}
.extra-sec .text-list .mint-cell .mint-cell-wrapper {
      padding-left: 0;
      padding-right: 0.66667rem;
}
.extra-sec .text-list .mint-cell .mint-cell-wrapper .is-link {
        font-size: 0.14667rem;
}
.extra-sec .text-list .mint-cell .mint-cell-text {
      color: #666;
}
.extra-sec .text-list .icon-arrow-right {
    margin-left: 0.26667rem;
}
.extra-sec .input-marks {
    padding: 0.06667rem 0.13333rem;
    width: 5.86667rem;
    text-align: right;
    font-size: 0.14667rem;
    resize: none;
    border: none;
}
.extra-sec .input-marks::placeholder {
      color: #BDBDBD;
}
.gift-sec {
  background-color: #fff;
}
.gift-sec .gift-header {
    text-align: center;
    line-height: 0.88rem;
    color: #030303;
}
.gift-sec .media {
    padding: 0.45333rem 0.72rem 0.45333rem 0.88rem;
    border-top: 1px solid #eeeeee;
}
.gift-sec .media .m-operate {
      padding-right: 0;
}
.firm-bottom-sec {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  font-size: 0.32rem;
  border-top: 1px solid #eee;
  background-color: #fff;
}
.firm-bottom-sec .firm-bottom-content {
    display: flex;
}
.firm-bottom-sec .firm-bottom-content .firm-bottom-left {
      padding: 0 0.26667rem 0 0.53333rem;
      flex: 1;
      display: inline-flex;
      align-items: center;
      justify-content: space-between;
}
.firm-bottom-sec .firm-bottom-content .firm-bottom-left .text-gray {
        margin-left: 0.26667rem;
}
.firm-bottom-sec .firm-bottom-content .checkout-btn {
      width: 3.06667rem;
      height: 1.53333rem;
      line-height: 1.53333rem;
      text-align: center;
      color: #fff;
      font-size: 0.42667rem;
      background-color: #D8331D;
}
.firm-bottom-sec .firm-bottom-content .checkout-btn.disabled {
        background-color: #CCCCCC;
}
.mint-datetime-action {
  color: #544014;
  font-size: 0.32rem;
}
.deliverPick {
  width: 100%;
}



