
.empty-sec {
  padding: 1.33333rem 0;
  color: #666666;
  text-align: center;
}
.empty-footer-sec {
  padding: 0.53333rem 0;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  font-size: 0.37333rem;
  color: #D8331D;
  text-align: center;
  border-top: 1px solid #ccc;
}
.address-list {
  padding-left: 0.66667rem;
}
.address-list .mint-cell-wrapper {
    padding: 0;
}
.address-list .mint-cell-wrapper .mint-cell-title {
      display: none;
}
.address-list .mint-cell-swipe .mint-cell-value {
    width: 100%;
}
.address-list .address-sec {
    padding-left: 0;
    border: none;
    border-bottom: 1px solid #ccc;
}
.address-list .address-sec.disabled {
      color: #ccc;
}
.address-list .address-sec.disabled .big-size {
        color: #ccc;
}
.address-list .mint-cell-swipe-buttongroup {
    display: flex;
}
.address-list .mint-cell-swipe-button {
    width: 1.46667rem;
    vertical-align: top;
    color: #fff;
    line-height: 1.2em;
    text-align: center;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}



