
.flex-bar {
  padding: 0 0.4rem;
  display: flex;
  justify-content: space-around;
  height: 1.33333rem;
  line-height: 1.33333rem;
  border-bottom: 1px solid #eee;
}
.flex-title {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}
.edite {
  display: inline-block;
  width: 0.8rem;
  color: #666;
}
.birth-branch-sec {
  padding: 0.53333rem;
  width: 100%;
}
.birth-branch-sec .branch-title {
    font-size: 0.26667rem;
    color: #666;
}
.birth-branch-sec .branch-list {
    margin-top: 20px;
    margin-left: 0.8rem;
}
.birth-branch-sec .branch-list li {
      line-height: 0.85333rem;
}
.birth-branch-sec .branch-list li:last-of-type {
        padding-left: 0.4rem;
}
.birth-branch-sec .branch-other {
    padding: 0 0.26667rem;
    width: 100%;
    border: 1px solid #666666;
    line-height: 0.88rem;
}
.birth-branch-sec .branch-other.error {
      border-color: #D8331D;
}
.input-radio {
  -webkit-appearance: radio;
  margin-right: 0.13333rem;
}



