
.userAccountSet-cell {
  font-size: 0.32rem;
  margin-left: 0.66667rem;
  margin-right: 0px;
  height: 1.62667rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /*line-height: pxToRem(135px);*/
}
#userAccountSet-unbind {
  font-size: 0.32rem;
  margin-right: 0.26667rem;
  color: #191919;
  margin-top: 0px;
  margin-bottom: 0px;
  /*background-color: #26a2ff;*/
  vertical-align: text-top;
}
.userAccountSet-cell-leftTitle {
  color: #BDBDBD;
  display: inline-block;
  height: 1.62667rem;
  line-height: 1.62667rem;
}
.userAccountSet-cell-rightView {
  margin-right: 0.69333rem;
  font-size: 0.32rem;
  text-align: right;
  height: 1.62667rem;
  align-items: center;
  display: flex;
}
.userAccountSet-sparator {
  height: 1px;
  background-color: #d8d8d8;
  margin-left: 0.66667rem;
}
#userAccountSet-userIcon {
  width: 0.93333rem;
  height: 0.93333rem;
  background: #EEEEEE;
  -moz-border-radius: 0.46667rem;
  -webkit-border-radius: 0.46667rem;
  border-radius: 0.46667rem;
  border-width: 0px;
  margin-right: 0.26667rem;
  border-color: #000 #000 #000 #000;
}
.logout {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1.46667rem;
  color: #191919;
  font-size: 0.37333rem;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
}
.sparator2 {
  height: 1px;
  background-color: #d8d8d8;
  position: absolute;
  top: 0px;
  left: 0;
  right: 0;
}



