
.order-tab {
  display: flex;
  justify-content: space-around;
  background-color: #fff;
}
.order-tab .tab-item {
    display: inline-block;
    padding: 0.33333rem 0.13333rem;
}
.order-tab .tab-item.active {
      color: #D4B675;
      border-bottom: 2px solid #D4B675;
}
.order-list-sec .mint-loadmore {
  background-color: #eee;
}
.order-items {
  padding-left: 0.66667rem;
  margin-top: 0.53333rem;
  background-color: #fff;
}
.order-sec {
  background-color: #fff;
}
.order-sec .gift-header {
    padding: 0 0.66667rem 0 0;
    text-align: center;
    line-height: 0.88rem;
    color: #030303;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 1.46667rem;
}
.order-sec .media {
    padding: 0.45333rem 0.72rem 0.45333rem 0;
    border-top: 1px solid #eeeeee;
}
.order-sec .media .img-list {
      display: inline-flex;
      flex: 1;
}
.order-sec .media .img-list .img-wrap {
        width: 90%;
        white-space: nowrap;
        overflow: auto;
}
.order-sec .media .img-list img {
        display: inline-block;
        width: 2.13333rem;
        height: 2.13333rem;
}
.order-sec .media .img-list img:not(:last-of-type) {
          margin-right: 0.26667rem;
}
.order-sec .media .media-content {
      padding-left: 0.26667rem;
      display: inline-flex;
      align-items: center;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
}
.order-sec .media .media-content > div {
        width: 100%;
}
.order-sec .media .media-right {
      display: inline-flex;
      align-items: center;
}
.order-foot {
  padding: 0.26667rem 0.66667rem;
  border-top: 1px solid #CCCCCC;
  text-align: right;
}



