
.order-header-sec {
  padding-left: 0.66667rem;
  background-color: #fff;
}
.order-header-sec .address-sec {
    padding-left: 0;
}
.order-timer-list {
  padding: 0.26667rem 0;
  border-bottom: 1px solid #ccc;
}
.order-timer-list li {
    padding: 0.13333rem 0;
}
.order-timer-list li .text-grey {
      display: inline-block;
      width: 1.6rem;
      color: #666666;
}
.price-sec {
  padding: 0.33333rem 0.66667rem 0.33333rem 0;
  display: flex;
  justify-content: space-between;
}
.price-sec .label {
    display: inline-block;
    width: 1.33333rem;
}
.pro-sec {
  margin: 0 0 0.53333rem 0;
  padding: 0 0 0 0.66667rem;
  background-color: #fff;
}
.pro-sec .gift-sec {
    background-color: #fff;
}
.pro-sec .gift-sec .gift-header {
      padding: 0.48rem 0.66667rem 0.48rem 0;
      display: flex;
      justify-content: space-between;
      color: #030303;
}
.pro-sec .gift-sec .media {
      padding: 0.45333rem 0.72rem 0.45333rem 0;
      border-top: 1px solid #eeeeee;
}
.pro-sec .gift-sec .media .m-operate {
        padding-right: 0;
}
.pro-sec .order-details-foot {
    padding: 0.48rem 0.48rem 0.48rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #eeeeee;
    color: #000;
}
.pro-sec .order-details-foot .details-info p {
      padding: 0.04rem 0;
}
.pro-sec .order-details-foot .text-grey {
      margin-right: 0.26667rem;
      color: #666;
}
.order-type-foot {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  line-height: 1.46667rem;
  text-align: center;
  background-color: #fff;
  font-size: 0.37333rem;
}



