
.adressBar {
  padding: 0 0.4rem;
  display: flex;
  justify-content: space-around;
  height: 1.33333rem;
  line-height: 1.33333rem;
}
.adressNav {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  font-size: 0;
}
.adressNav .adress-adress-group {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.32rem;
}
.adressNav .adress-adress-group .icon-adress {
      margin-right: 0.26667rem;
      width: 0.26667rem;
}



