
.cancel-info-sec {
  position: relative;
}
.cancel-info-sec .info-item {
    margin-top: 0.26667rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cancel-info-sec .info-item .text {
      padding-left: 0.33333rem;
      margin-left: 0.53333rem;
      width: 7.92rem;
      height: 1.6rem;
      color: #BDBDBD;
      background-color: #fff;
      display: inline-flex;
      align-items: center;
}
.cancel-info-sec .info-item .icon-circel {
      display: inline-block;
      width: 10px;
      height: 10px;
      border: 1px solid #ccc;
      border-radius: 50%;
      position: relative;
      background: #eee;
      z-index: 999;
}
.cancel-info-sec .info-item.active .text {
      color: #191919;
}
.cancel-info-sec .info-item.active .icon-circel {
      border-color: #D8331D;
}
.line {
  width: 1px;
  background-color: #ccc;
  position: absolute;
  left: 0.77333rem;
  top: 1.33333rem;
  bottom: 0;
  z-index: 0;
}



