
.flex-bar {
  padding: 0 0.4rem;
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  height: 1.33333rem;
  line-height: 1.33333rem;
  border-bottom: 1px solid #eee;
}
.flex-title {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}
.flex-right {
  width: 0.93333rem;
  color: #666666;
}
.reason-sec {
  padding: 0.61333rem 0.66667rem;
  display: flex;
  justify-content: space-between;
  width: 100%;
  background: #fff;
}
.cancel-tips {
  padding: 0.53333rem 0.66667rem;
}
.cancel-tips p:not(:first-of-type) {
    margin-top: 0.13333rem;
    color: #666;
}
.reason-picker {
  width: 100%;
}



