
#deliveryArea {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.deliveryArea-header {
  position: fixed;
  height: 1.33333rem;
  top: 0px;
  left: 0.4rem;
  right: 0.4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.deliveryArea-header-close {
  width: 0.26667rem;
}
.deliveryArea-header-title {
  font-size: 0.32rem;
  color: #191919;
}
.deliveryArea-search {
  position: fixed;
  height: 1.78667rem;
  left: 0;
  right: 0;
  top: 1.33333rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.deliveryArea-content {
  position: absolute;
  top: 3.12rem;
  left: 0;
  right: 0;
  bottom: 0px;
}
.deliveryArea-content-locationView {
  z-index: 666;
  background-color: transparent;
}
#map-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /*z-index: 100;*/
  /*margin-top: pxToRem(234px);*/
  height: 100%;
  /*!*height: pxToRem(970px);*!*/
  /*background-color: #26a2ff;*/
}
.deliveryArea-hr {
  position: absolute;
  top: 1.33333rem;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #d8d8d8;
}
.deliveryArea-search-textfield {
  height: 0.88rem;
  width: 86.666666666%;
  border: solid 1px #666666;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
#deliveryArea-search-img {
  width: 0.46667rem;
  margin-left: 0.26667rem;
}
.deliveryArea-search-lable {
  color: #9b9b9b;
  margin-left: 0.26667rem;
}
.deliveryArea-search-lable {
  width: 70%;
}
.deliveryArea-content-resultView {
  background-color: transparent;
  margin-top: 0.13333rem;
  margin-left: 0.66667rem;
  margin-right: 0.66667rem;
  height: 8rem;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  overflow: scroll;
  flex-direction: column;
}
.deliveryArea-content-addr {
  color: #191919;
  font-size: 0.32rem;
  margin-bottom: 0.13333rem;
  background-color: white;
  width: 100%;
}
.deliveryArea-content-lable {
  background-color: white;
  margin-top: 0.26667rem;
  margin-bottom: 0.26667rem;
}



