
.shopcart-wrap {
  padding-bottom: 2rem;
}
.grey-bg-sec {
  padding-bottom: 0.26667rem;
  background-color: #eee;
}
.part-border {
  border: 1px solid #BDBDBD;
}
.part-table-footer .part-mark {
  width: 100%;
  text-align: right;
  border: none;
}
.cart-swiper:not(:first-of-type) {
  margin-top: 0.26667rem;
}
.cart-swiper .pro-table-sec {
  width: 100%;
  background-color: #fff;
}
.cart-swiper .mint-cell-wrapper {
  padding: 0;
}
.cart-swiper .mint-cell-wrapper .mint-cell-title {
    display: none;
}
.cart-swiper .mint-cell-wrapper .mint-cell-value {
    width: 100%;
}
.cart-swiper .mint-cell-swipe-button {
  padding: 0;
  width: 2rem;
  display: flex;
  align-items: center;
  test-align: cener;
  justify-content: center;
  font-size: 0.32rem;
}
.car-bottom-sec {
  position: fixed;
  bottom: 1.46667rem;
  left: 0;
  width: 100%;
  font-size: 0.32rem;
  border-top: 1px solid #eee;
  background-color: #fff;
}
.car-bottom-sec.bottom {
    bottom: 0;
}
.car-bottom-sec .car-bottom-content {
    display: flex;
}
.car-bottom-sec .car-bottom-content .car-bottom-left {
      padding: 0 0.26667rem 0 0.53333rem;
      flex: 1;
      display: inline-flex;
      align-items: center;
      justify-content: space-between;
}
.car-bottom-sec .car-bottom-content .car-bottom-left .text-gray {
        margin-left: 0.26667rem;
}
.car-bottom-sec .car-bottom-content .checkout-btn {
      width: 3.06667rem;
      height: 1.53333rem;
      line-height: 1.53333rem;
      text-align: center;
      color: #fff;
      font-size: 0.42667rem;
      background-color: #D8331D;
}
.car-bottom-sec .car-bottom-content .checkout-btn.disabled {
        background-color: #CCCCCC;
}
.checkbox-group-content {
  display: flex;
  align-items: center;
}
.birth-wrap {
  width: 100%;
  height: 100%;
}
.empty {
  margin-top: 1.36rem;
  text-align: center;
  color: #666666;
}



