
#searchCity {
  background-color: white;
  display: inline;
  position: fixed;
  bottom: 0px;
  left: 0px;
  right: 0px;
  top: 0px;
  z-index: 666;
  overflow: scroll;
}
#search {
  height: 1.33333rem;
  position: relative;
}
#search-img {
  display: inline-block;
  margin-left: 0.8rem;
  margin-top: 0.46667rem;
  height: auto;
  width: 0.4rem;
}
#search-text-view {
  display: inline-block;
  position: absolute;
  left: 1.65333rem;
  top: 0px;
  bottom: 0px;
}
#search-text2 {
  margin-top: 0.42667rem;
}
#search-close {
  position: absolute;
  right: 0.98667rem;
  width: 0.26667rem;
  height: auto;
  top: 0.53333rem;
}
#searchCity-parent {
  position: relative;
  overflow: auto;
  isVertical: true;
}



