
#picker {
  background: rgba(25, 25, 25, 0.3);
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  z-index: 666;
}
#picker-view {
  position: absolute;
  height: 6.26667rem;
  background-color: #EEEEEE;
  bottom: 0px;
  left: 0px;
  right: 0px;
}
#picker-wrapper {
  bottom: 0px;
  top: 1.33333rem;
  background-color: #EEEEEE;
}
.picker-view-header {
  height: 1.33333rem;
  background-color: white;
  left: 0;
  right: 0;
  top: 0;
}
.picker-center-highlight:before, .picker-center-highlight:after {
  background-color: #BDBDBD;
}
.picker-items {
  background-color: #EEEEEE;
}
.picker-view-lable {
  color: #191919;
  font-size: 0.32rem;
  height: 1.33333rem;
  line-height: 1.33333rem;
  display: inline-block;
}
.picker-view-lable-commit {
  margin-left: 7.68rem;
}
.picker-view-lable-cancel {
  margin-left: 0.53333rem;
}



