
.flex-bar {
  padding: 0 0.4rem;
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  height: 1.33333rem;
  line-height: 1.33333rem;
  border-bottom: 1px solid #eee;
}
.flex-title {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}



