
* {
  -webkit-tap-highlight-color: transparent;
  -ms-tap-highlight-color: transparent;
  tap-highlight-color: transparent;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  cursor: pointer;
}
::-webkit-scrollbar {
  width: 0;
  height: 0;
  background-color: rgba(240, 240, 240, 0);
}
body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
fieldset,
lengend,
button,
input,
textarea,
th,
td {
  margin: 0;
  padding: 0;
}
body,
button,
input,
select,
textarea {
  font-family: '\5fae\8f6f\96c5\9ed1', Tahoma, Helvetica, Arial, '\5b8b\4f53', sans-serif;
}
h1 {
  font-size: 18px;
}
h2 {
  font-size: 16px;
}
h3 {
  font-size: 14px;
}
h4,
h5,
h6 {
  font-size: 100%;
}
address,
cite,
dfn,
em,
var {
  font-style: normal;
}
code,
kbd,
pre,
samp,
tt {
  font-family: "Courier New", Courier, monospace;
}
small {
  font-size: 12px;
}
ul,
ol {
  list-style: none;
}
a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:active {
    text-decoration: none;
}
abbr[title],
acronym[title] {
  border-bottom: 1px dotted;
  cursor: help;
}
q:before,
q:after {
  content: '';
}
legend {
  color: #000;
}
fieldset,
img {
  border: none;
}
button,
input,
select,
textarea {
  font-size: 100%;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
hr {
  border: none;
  height: 1px;
}
html {
  overflow-y: scroll;
}
input {
  -webkit-appearance: none;
}
input,button,select,textarea{outline:none;  border:none;
}
textarea{resize:none
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  display: block;
  margin: 0;
  padding: 0;
}
html {
  font-size: 312.5%;
  color: #333;
}
@media screen and (max-width: 359px) {
html {
    font-size: 266.67%;
}
}
@media screen and (min-width: 360px) and (max-width: 374px) {
html {
    font-size: 300%;
}
}
@media screen and (min-width: 384px) and (max-width: 399px) {
html {
    font-size: 320%;
}
}
@media screen and (min-width: 400px) and (max-width: 413px) {
html {
    font-size: 333.33%;
}
}
@media screen and (min-width: 414px) and (max-width: 431px) {
html {
    font-size: 345%;
}
}
@media screen and (min-width: 432px) and (max-width: 479px) {
html {
    font-size: 360%;
}
}
@media screen and (min-width: 480px) and (max-width: 639px) {
html {
    font-size: 400%;
}
}
@media screen and (min-width: 640px) {
html {
    font-size: 533.33%;
}
}



