
.acces-border {
  border: 1px solid #BDBDBD;
}
.acces-table-footer {
  padding-right: 0.53333rem;
}
.acces-table-footer .acces-mark {
    border: none;
}
.accessories-sec {
  margin-top: 0.26667rem;
  width: 100%;
  background-color: #fff;
  color: #666;
}
.accessories-sec .part-header {
    padding: 0.26667rem 0.53333rem 0.26667rem 0.8rem;
    display: flex;
    justify-content: space-between;
    font-size: 0.26667rem;
}
.accessories-sec .part-list {
    padding: 0 0.66667rem 0.8rem 0.66667rem;
    font-size: 0;
}
.accessories-sec .part-list .part-item {
      margin-top: 0.46667rem;
      display: inline-block;
      width: 4.16rem;
      height: 4.02667rem;
      border: 1px solid #ccc;
}
.accessories-sec .part-list .part-item:nth-of-type(2n+1) {
        margin-right: 0.34667rem;
}
.accessories-sec .part-list .part-item .part-img {
        margin: 0.50667rem auto 0.4rem;
        width: 2.29333rem;
        height: 2.29333rem;
        text-align: center;
}
.accessories-sec .part-list .part-item .part-img img {
          width: 100%;
          max-width: 100%;
}
.accessories-sec .part-list .part-operate {
      padding: 0 0.26667rem;
      display: flex;
      justify-content: space-between;
      font-size: 0.26667rem;
}
.accessories-sec .part-list .part-operate .text-gray {
        font-size: 0.24rem;
}
.accessories-sec .part-list .part-operate .part-btn {
        display: inline-block;
        width: 1.30667rem;
        height: 0.56rem;
        line-height: 0.53333rem;
        text-align: center;
        font-size: 0.32rem;
        border: 1px solid #666;
}



