
#mineLogin {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.loginEntrance-header {
  /*height: pxToRem(100px);*/
  height: 7.49%;
  position: fixed;
  top: 0;
  left: 0.4rem;
  right: 0.4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.loginEntrance-header-close {
  width: 0.26667rem;
}
.loginEntrance-header-register {
  font-size: 0.32rem;
}
.loginEntrance-content-img {
  width: 100%;
  /*overflow: auto;*/
}
.loginEntrance-content {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: auto;
  position: fixed;
  bottom: 16.79%;
  top: 7.49%;
  left: 0.4rem;
  right: 0.4rem;
}
.loginEntrance-footer {
  height: 16.79%;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loginEntrance-footer-login {
  height: 1.33333rem;
  width: 9.2rem;
  background: #3D2D23;
  color: white;
  text-align: center;
  line-height: 1.33333rem;
  font-size: 0.37333rem;
}



